




















import Vue from "vue";
import { mapMutations } from "vuex";

export default Vue.extend({
  name: "AutocompleteInput",
  data() {
    return {
      search: this.$store.state.search
    };
  },
  methods: {
    ...mapMutations(["updateSearch"]),
    onSubmit() {
      this.updateSearch(this.search);
    },
    onChange() {
      this.updateSearch(this.search);
    }
  }
});
